import Api from '@/common/api';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
let userInfo = uni.getStorageSync('userInfo');
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition: 'fixed',
			headerTop: '0px',
			keyword: '',
			loadingType: 'more', //加载更多状态
			filterIndex: 0,
			cateId: 0, //已选三级分类id
			pageNum: 1,
			cid: null,
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			saleSort:0,//1代表销量排序
			cateList: [],
			goodsList: []
		};
	},

	onLoad(options) {



		this.keyword = options.keyword;
		this.cateId = options.tid;
		this.loadCateList(options.fid, options.sid);
		this.loadData();
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = 'fixed';
		} else {
			this.headerPosition = 'absolute';
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.pageNum = this.pageNum + 1;
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.pageNum = this.pageNum + 1;
		this.loadData();
	},
	methods: {
		//加载分类
		async loadCateList(fid, sid) {
			let params = {};
			let data = await Api.apiCall('get', Api.goods.typeGoodsList, params);
			//let list = await this.$api.json('cateList');
			data = data.data;
			let cateList = data.filter(item => item.level == "L1");

			cateList.forEach(item => {
				let tempList = data.filter(val => val.pid == item.id);
				item.child = tempList;
			});
			this.cateList = cateList;
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = 'add', loading) {
			//没有更多直接返回
			if (type === 'add') {
				if (this.loadingType === 'nomore') {
					return;
				}
				this.loadingType = 'loading';
			} else {
				this.loadingType = 'more';
			}
			let params = {
					pageNum: this.pageNum,
					keyword: this.keyword,
					cateId:this.cateId,
					priceOrder:this.priceOrder,
					saleSort:this.saleSort
				};
			
			if (type === 'refresh') {
				this.goodsList = [];
			}
			let list = await Api.apiCall('get', Api.goods.goodsList, params);

			let goodsList = list.data.records;
		

		this.goodsList = this.goodsList.concat(goodsList);

		//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
		this.loadingType = this.goodsList.length > list.total ? 'nomore' : 'more';
		if (type === 'refresh') {
			if (loading == 1) {
				uni.hideLoading();
			} else {
				uni.stopPullDownRefresh();
			}
		}
	},
	//筛选点击
	tabClick(index) {
		this.pageNum = 1;
		if (this.filterIndex === index && index !== 2) {
			return;
		}
		this.filterIndex = index;
		if (index === 1) {
			this.saleSort = 1
		} else {
			this.saleSort = 0;
		}
		if (index === 2) {
			this.priceOrder = this.priceOrder === 1 ? 2 : 1;
		} else {
			this.priceOrder = 0;
		}
		uni.pageScrollTo({
			duration: 300,
			scrollTop: 0
		});
		this.loadData('refresh', 1);
		uni.showLoading({
			title: '正在加载'
		});
	},
	//显示分类面板
	toggleCateMask(type) {
		let timer = type === 'show' ? 10 : 300;
		let state = type === 'show' ? 1 : 0;
		this.cateMaskState = 2;
		setTimeout(() => {
			this.cateMaskState = state;
		}, timer);
	},
	//分类点击
	changeCate(item) {
		this.pageNum = 1;
		this.cateId = item.id;
		this.toggleCateMask();
		uni.pageScrollTo({
			duration: 300,
			scrollTop: 0
		});
		this.loadData('refresh', 1);
		uni.showLoading({
			title: '正在加载'
		});
	},
	//详情
	navToDetailPage(item) {
		//测试数据没有写id，用title代替
		let id = item.id;
		uni.navigateTo({
			url: `/pages/product/product?id=${id}`
		});
	},
	stopPrevent() {}
}
};
